.nav-toggle {
    display: none;
}

.mobile-nav {
position: fixed;
top: -100%;
left: 0;
width: 100%;
height: 100%;
background-color: rgba(0, 0, 0, 0.9);
transition: top 0.5s ease-in-out;
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
gap: 20px;
z-index: 99;
}

.mobile-nav.visible {
top: 0;
}

.mobile-nav a {
color: white;
text-decoration: none;
font-size: 2em;
transition: color 0.3s ease-in-out;
}

.mobile-nav a:hover {
color: #ff0000;
}

nav {
position: sticky;
top: 0;
display: flex;
flex-direction: row;
align-items: center;
font-family: 'Madimi_One', sans-serif;
padding: 10px;
z-index: 1;
}

nav img {
margin: 10px;
height: 60px;
}

nav ul {
margin: auto;
display: flex;
flex-direction: row;
list-style: none;
gap: 50px;
}

nav ul a {
text-decoration: none;
display: flex;
align-items: center;
justify-content: center;
font-size: 1.4em;
color: aliceblue;
}

nav ul a img {
width: 50px;
height: 45px;
}

nav ul a.activeLink {
color: red;
}

@media screen and (max-width: 1030px) {
.nav-toggle {
    width: 300%;
    display: block;
    position: fixed;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 100;
    background: transparent;
    border: none;
}


nav ul {
  display: none;
}
}
