@media screen and (max-width: 1150px){
  .wrapper{
    grid-template-columns: 1fr 1fr;
  }

  @media screen and (max-width: 800px){
    .wrapper{
      margin: auto;
      grid-template-columns: 1fr;
    }
  }
}