
#about > div > p{
    width: 50%;
    font-size: 2em;
    margin: 1rem 0;
    font-family:'Source_Code_Pro';

}

#about > header{
    height: 30vh;
}

#about > h2{
    position: relative;
    font-size: 3em;
    font-family:'Source_Code_Pro';
    margin: 1rem 0;
    text-align: center;
}

#about{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 100px;
    width: 100%;
    overflow-x: hidden;
}

span{
    color: red;
}

#about > div{

    padding: 0 100px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

#cv_video {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    object-fit: cover;
    z-index: 5;
    transition: all 5s ease-in-out;
  }
  
  #cv_video.smallSize {
    position: absolute;
    width: 100%;
    height: 400px;
    top: 50%;
    transform: translateY(-140%);
    z-index: -1;
    border-radius: 30px;
  }
  
  #div_video_cv > video{
    width: 40%;
  }

  @media screen and (max-width: 1000px) {
    #about > h1{
        top: 200px;
        font-size: 2em;
    }
    #about{
        gap: 10px;
        width: 100%;
    }

    #about > h2{
        font-size: 2em;
    }
    #about > div{
        padding: 2px;
        width: 100%;
        flex-direction: column;
        gap: 50px;
    }

    #about > div > img{
        width: 60%;
    }
    #about > div > p{
        font-size: 1em;
        width: 100%;
    }
    
    #div_video_cv > video{
        width: 100%;
    }
    @media screen and (max-width: 820px){
    #about > header > h1{
        position: relative;
        top: 0px;
        font-size: 5em;
    } 
    }
  }